.gh-postfeed .post-card-small:nth-child(n+2) {
    border-top: 1px solid var(--color-border);
    padding-top: 2em;
    margin-top: 1.5em;
}

.post-card-small {

}

.post-card-small-inner {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
}

@media (min-width: 500px) {
    .post-card-small-inner {
        flex-wrap: nowrap;
    }
}

.post-card-small-image {
    width: 100%;
    margin-bottom: 0.3em;
}

@media (min-width: 500px) {
    .post-card-small-image {
        width: 30vw;
        max-width: 140px;
        margin-bottom: 0;
        margin-right: 20px;
        flex-shrink: 0;
    }
}

.post-card-small-text {
    width: 100%;
    flex-grow: 2;
}

@media (min-width: 500px) {
    .post-card-small-text {

    }
}

.post-card-small-meta {
    margin-top: -0.4em;
    opacity: 0.5;
}

.post-card-small-title {
    margin-top: 0.2em;
    margin-bottom: 0.2em;
}

.post-card-small-excerpt {
    opacity: 0.5;
    transition: opacity 0.3s ease;
}

.post-card-small a:hover .post-card-small-title {
    color: var(--ghost-accent-color);
}

.post-card-small a:hover .post-card-small-excerpt {
    opacity: 1;
}
